er-color: rgb(0, 165, 0);\r\n    border-width: 2px;\r\n}\r\n\r\nhr.emphasis {\r\n    border-color: rgb(0, 127, 51);\r\n    width: 15%;\r\n    border-width: 7px;\r\n}\r\n\r\nhr.style-two {\r\n    border: 0;\r\n    height: 2px;\r\n    background-image: linear-gradient(to right, rgba(0, 128, 0, 0.103), rgba(0, 128, 0, 0.5), rgb(0, 165, 0));\r\n}\r\n\r\nhr.style-three {\r\n    border: 0;\r\n    height: 2px;\r\n    background-image: linear-gradient(to right, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.9));\r\n}\r\n\r\nhr.style-eight {\r\n    overflow: visible;\r\n    /* For IE */\r\n    padding: 0;\r\n    border: none;\r\n    border-top: medium double #333;\r\n    color: #333;\r\n    text-align: center;\r\n}\r\n\r\nhr.style-eight:after {\r\n    content: \"\";\r\n    display: inline-block;\r\n    position: relative;\r\n    top: -0.9em;\r\n    font-size: 1.5em;\r\n    padding: 0 0.4em;\r\n    background: white;\r\n}\r\n\r\na {\r\n    color: #212529;\r\n    -webkit-transition: all 0.2s;\r\n    transition: all 0.2s;\r\n}\r\n\r\na:focus,\r\na:hover {\r\n    color: rgb(211, 211, 211);\r\n}\r\n\r\nunder {\r\n    text-decoration: underline;\r\n}\r\n\r\nblockquote {\r\n    font-style: italic;\r\n    color: #868e96;\r\n}\r\n\r\n\r\n/*names and photos*/\r\n\r\n.position {\r\n    margin: 0px;\r\n    text-align: center;\r\n    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;\r\n    font-size: 16px;\r\n    color: white;\r\n    padding-left: 15px;\r\n    padding-right: 15px;\r\n}\r\n\r\n.position_name {\r\n    margin: 0px;\r\n    font-size: 22px;\r\n    color: rgb(0, 165, 0);\r\n}\r\n\r\n.pic_row {\r\n    display: inline-flex;\r\n    width: 100%;\r\n}\r\n\r\n.pic_item {\r\n    width: 50%;\r\n    text-align: center;\r\n}\r\n\r\n.profileImg {\r\n    width: 70%;\r\n    border-radius: 50%;\r\n    border-width: 1px;\r\n}\r\n\r\n.profileImgSbs {\r\n    width: 80%;\r\n    border-radius: 3%;\r\n    border-width: 1px;\r\n}\r\n\r\n\r\n/*names and photos responsive*/\r\n\r\n@media(max-width:768px) {\r\n    .pic_row {\r\n        display: block;\r\n    }\r\n    .pic_item {\r\n        width: 100%;\r\n    }\r\n}\r\n\r\n.section-heading {\r\n    font-size: 32px;\r\n    font-weight: 700;\r\n    margin-top: 60px;\r\n    text-align: center;\r\n}\r\n\r\n.caption {\r\n    font-size: 14px;\r\n    font-style: italic;\r\n    display: block;\r\n    margin: 0;\r\n    padding: 10px;\r\n    text-align: center;\r\n    border-bottom-right-radius: 5px;\r\n    border-bottom-left-radius: 5px;\r\n}\r\n\r\n ::-moz-selection {\r\n    color: #fff;\r\n    background: #0085A1;\r\n    text-shadow: none;\r\n}\r\n\r\n ::selection {\r\n    color: #fff;\r\n    background: #0085A1;\r\n    text-shadow: none;\r\n}\r\n\r\nimg::-moz-selection {\r\n    color: #fff;\r\n    background: transparent;\r\n}\r\n\r\nimg::selection {\r\n    color: #fff;\r\n    background: transparent;\r\n}\r\n\r\nimg::-moz-selection {\r\n    color: #fff;\r\n    background: transparent;\r\n}\r\n\r\n#mainNav {\r\n    position: absolute;\r\n    border-bottom: 1px solid #e9ecef;\r\n    background-color: rgb(255, 255, 255);\r\n    font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;\r\n    padding: 12px;\r\n}\r\n\r\n#mainNav .navbar-brand {\r\n    font-weight: 800;\r\n    font-size: 24px;\r\n    color: #343a40;\r\n}\r\n\r\n#mainNav .navbar-toggler {\r\n    font-size: 12px;\r\n    font-weight: 800;\r\n    padding: 13px;\r\n    text-transform: uppercase;\r\n    color: #343a40;\r\n}\r\n\r\n#mainNav .navbar-nav>li.nav-item>a {\r\n    font-size: 14px;\r\n    font-weight: 800;\r\n    letter-spacing: 0px;\r\n    text-transform: uppercase;\r\n}\r\n\r\n\r\n/* Dropdown Button */\r\n\r\n#mainNav .dropbtn {\r\n    font-size: 13px;\r\n    color: black;\r\n    font-weight: 600;\r\n    letter-spacing: 1px;\r\n    text-transform: uppercase;\r\n    border: none;\r\n    background-color: transparent;\r\n    padding: 10px 25px;\r\n    z-index: 1.1;\r\n}\r\n\r\n#mainNav .dropbtn:hover {\r\n    text-decoration: none;\r\n    color: rgb(0, 176, 79);\r\n}\r\n\r\n\r\n/* Dropdown button on hover & focus\r\n\r\n#mainNav .dropbtn:hover,\r\n#mainNav .dropbtn:focus {\r\n    /*background-color: rgb(0, 165, 0);*/\r\n\r\n#mainNav i {\r\n    border: solid white;\r\n    border-width: 0px 3px 3px 0px;\r\n    display: inline-block;\r\n    padding: 0px;\r\n}\r\n\r\n#mainNav .down {\r\n    transform: rotate(45deg);\r\n    -webkit-transform: rotate(45deg);\r\n}\r\n\r\n\r\n/* The container <div> - needed to position the dropdown content */\r\n\r\n#mainNav .dropdown {\r\n    position: relative;\r\n    display: inline-block;\r\n}\r\n\r\n\r\n/* Dropdown Content (Hidden by Default) */\r\n\r\n#mainNav .dropdown-content {\r\n    display: none;\r\n    position: absolute;\r\n    background-color: #ffffff;\r\n    min-width: 280px;\r\n    z-index: 1;\r\n    padding-top: 18px;\r\n}\r\n\r\n\r\n/* Links inside the dropdown */\r\n\r\n#mainNav .dropdown-content a {\r\n    color: black;\r\n    font-size: 14px;\r\n    padding: 12px 16px;\r\n    text-decoration: none;\r\n    display: block;\r\n}\r\n\r\n\r\n/* Change color of dropdown links on hover */\r\n\r\n#mainNav .dropdown-content a:hover {\r\n    background-color: #ddd\r\n}\r\n\r\n#mainNav .dropdown:hover .dropdown-content {\r\n    display: block;\r\n}\r\n\r\n\r\n/* Show the dropdown menu (use JS to add this class to the .dropdown-content container when the user clicks on the dropdown button) */\r\n\r\n#mainNav .show {\r\n    display: block;\r\n}\r\n\r\n@media only screen and (min-width: 992px) {\r\n    #mainNav {\r\n        border-bottom: 1px solid transparent;\r\n        background: transparent;\r\n        background-color: rgb(255, 255, 255);\r\n    }\r\n    #mainNav .navbar-brand {\r\n        padding: 10px 20px;\r\n        color: #fff;\r\n    }\r\n    #mainNav .navbar-brand:focus,\r\n    #mainNav .navbar-brand:hover {\r\n        color: rgb(218, 219, 217);\r\n    }\r\n    #mainNav .navbar-nav>li.nav-item>a {\r\n        padding: 10px 20px;\r\n        color: black;\r\n    }\r\n    #mainNav .navbar-nav>li.nav-item>a:focus,\r\n    #mainNav .navbar-nav>li.nav-item>a:hover {\r\n        color: rgba(49, 49, 49, 0.8);\r\n    }\r\n}\r\n\r\n@media only screen and (min-width: 992px) {\r\n    @keyframes fade-in {\r\n        from {\r\n            opacity: 0;\r\n            transform: scale(.8, .8)\r\n        }\r\n        to {\r\n            opacity: 1;\r\n        }\r\n        100% {\r\n            opacity: 1;\r\n        }\r\n    }\r\n    .fade-in-element {\r\n        animation: fade-in 1.4s;\r\n    }\r\n    .hidden {\r\n        opacity: 0;\r\n        animation: fade-in 2s linear 1.5s 1 normal forwards;\r\n    }\r\n    .no-opacity {\r\n        opacity: 0;\r\n    }\r\n    #mainNav {\r\n        -webkit-transition: background-color 0.2s;\r\n        transition: background-color 0.2s;\r\n        /* Force Hardware Acceleration in WebKit */\r\n        -webkit-transform: translate3d(0, 0, 0);\r\n        transform: translate3d(0, 0, 0);\r\n        -webkit-backface-visibility: hidden;\r\n        background-color: rgb(255, 255, 255);\r\n        top: 0px;\r\n    }\r\n    #mainNav.is-fixed {\r\n        /* when the user scrolls down, we hide the header right above the viewport */\r\n        position: fixed;\r\n        top: -73px;\r\n        -webkit-transition: -webkit-transform 0.2s;\r\n        transition: -webkit-transform 0.2s;\r\n        transition: transform 0.2s;\r\n        transition: transform 0.2s, -webkit-transform 0.2s;\r\n        border-bottom: 1px solid white;\r\n        background-color: rgba(255, 255, 255, 1);\r\n    }\r\n    #mainNav.is-fixed .navbar-brand {\r\n        color: #242921;\r\n    }\r\n    #mainNav.is-fixed .navbar-brand:focus,\r\n    #mainNav.is-fixed .navbar-brand:hover {\r\n        color: white;\r\n    }\r\n    #mainNav.is-fixed .navbar-nav>li.nav-item>a {\r\n        color: #212529;\r\n    }\r\n    #mainNav.is-fixed .navbar-nav>li.nav-item>a:focus,\r\n    #mainNav.is-fixed .navbar-nav>li.nav-item>a:hover {\r\n        color: white;\r\n    }\r\n    #mainNav.is-fixed .dropbtn {\r\n        font-size: 12px;\r\n        font-weight: 700;\r\n        letter-spacing: 1px;\r\n        text-transform: uppercase;\r\n        border: none;\r\n        background-color: transparent;\r\n        color: black;\r\n        padding: 10px 25px;\r\n    }\r\n    #mainNav.is-fixed .dropbtn a:hover {\r\n        font-size: 13px;\r\n        text-decoration: none;\r\n        color: rgb(0, 165, 0);\r\n    }\r\n    #mainNav.is-fixed .dropbtn:hover,\r\n    #mainNav.is-fixed .dropbtn:focus {\r\n        color: white;\r\n    }\r\n    #mainNav.is-fixed i {\r\n        border: solid black;\r\n        border-width: 0px 3px 3px 0px;\r\n        display: inline-block;\r\n        padding: 3px;\r\n    }\r\n    #mainNav.is-fixed .down {\r\n        transform: rotate(45deg);\r\n        -webkit-transform: rotate(45deg);\r\n    }\r\n    #mainNav.is-visible {\r\n        /* if the user changes the scrolling direction, we show the header */\r\n        -webkit-transform: translate3d(0, 100%, 0);\r\n        transform: translate3d(0, 100%, 0);\r\n        background-color: rgba(255, 255, 255, 0.9)\r\n    }\r\n}\r\n\r\n\r\n/* The sidebar menu */\r\n\r\n.sidenav {\r\n    width: 175px;\r\n    /* Set the width of the sidebar */\r\n    position: fixed;\r\n    /* Fixed Sidebar (stay in place on scroll) */\r\n    z-index: 1;\r\n    /* Stay on top */\r\n    top: 180px;\r\n    /* Stay at the top */\r\n    left: 30px;\r\n    overflow-x: hidden;\r\n    /* Disable horizontal scroll */\r\n    padding-top: 5px;\r\n    text-align: right;\r\n}\r\n\r\n\r\n/* The navigation menu links */\r\n\r\n.sidenav a {\r\n    padding: 10px 3px 30px 3px;\r\n    text-decoration: none;\r\n    font-size: 14px;\r\n    color: rgb(182, 182, 182);\r\n    display: block;\r\n    font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;\r\n}\r\n\r\n.sidenav hr {\r\n    border-width: 1px;\r\n    border-color: rgba(182, 182, 182, 0.514);\r\n    border-style: groove;\r\n}\r\n\r\n\r\n/* When you mouse over the navigation links, change their color */\r\n\r\n.sidenav a:hover {\r\n    color: #f1f1f1;\r\n}\r\n\r\n\r\n/* On smaller screens, where height is less than 450px, change the style of the sidebar (less padding and a smaller font size) */\r\n\r\n@media screen and (max-height: 450px) {\r\n    .sidenav {\r\n        padding-top: 15px;\r\n        ;\r\n        left: 30px\r\n    }\r\n    .sidenav a {\r\n        font-size: 18px;\r\n    }\r\n}\r\n\r\n@media screen and (max-width: 1100px) {\r\n    .sidenav {\r\n        display: none\r\n    }\r\n    .no-opacity {\r\n        opacity: 1;\r\n    }\r\n    .hidden {\r\n        opacity: 1;\r\n    }\r\n}\r\n\r\nheader.masthead {\r\n    margin-bottom: 0px;\r\n    background: repeat center center;\r\n    background-color: #424242;\r\n    background-attachment: scroll;\r\n    position: relative;\r\n}\r\n\r\nheader.masthead .overlay {\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    height: 100%;\r\n    width: 100%;\r\n    background-image: linear-gradient(to right, rgba(0, 255, 0, 0.25), rgba(0, 100, 0, 0.1), rgba(0, 0, 255, 0.23));\r\n}\r\n\r\n.hero-bkg-animated {\r\n    background: gray url(https://subtlepatterns.com/patterns/geometry2.png) repeat 0 0;\r\n    width: 100%;\r\n    margin: 0;\r\n    text-align: center;\r\n    /*height: 300px;*/\r\n    padding-top: 0px;\r\n    box-sizing: border-box;\r\n    -webkit-animation: slide 15s linear infinite;\r\n}\r\n\r\n@-webkit-keyframes slide {\r\n    from {\r\n        background-position: 0 0;\r\n    }\r\n    to {\r\n        background-position: -400px 0;\r\n    }\r\n}\r\n\r\n\r\n/* Dropdown Content (Hidden by Default) */\r\n\r\n.header-button {\r\n    display: block;\r\n    float: right;\r\n    padding: 30px;\r\n    background-color: #f1f1f1;\r\n    min-width: 160px;\r\n    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);\r\n    z-index: 1;\r\n}\r\n\r\n\r\n/* Links inside the dropdow