ame}\", $color);\n\n  .border-#{$name} {\n    border-color: $color !important;\n  }\n}\n\n.card-img-100 {\n  width: 100px;\n  height: 100px;\n}\n\n.card-img-64 {\n  width: 64px;\n  height: 64px;\n}\n\n.mml-1 {\n  margin-left: - 0.25rem !important;\n}\n\n.flex-1 {\n  flex: 1;\n}\n","// Typography\n\nbody {\n  font-family: $roboto-font-family;\n  font-weight: 300;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  font-weight: 300;\n}\n\n// Responsive headings\n@each $key, $val in $grid-breakpoints {\n  @include media-breakpoint-up($key) {\n    $y: map-get($responsive-headings, $key);\n    @each $name, $value in $y {\n      .#{$name}-responsive {\n        font-size: $value;\n      }\n    }\n  }\n}\n\n// Divider\n.divider-new {\n  flex-direction: row;\n  justify-content: center;\n  align-items: center;\n  display: flex;\n  margin-top: $divider-margin-y;\n  margin-bottom: $divider-margin-y;\n  > h1,\n  h2,\n  h3,\n  h4,\n  h5,\n  h6 {\n    margin-bottom: 0;\n  }\n  &:before,\n  &:after {\n    content: '';\n    height: 1.5px;\n    flex: 1;\n    height: $divider-height;\n    background: #c6c6c6;\n  }\n  &:before {\n    margin: 0 $divider-margin-x 0 0;\n  }\n  &:after {\n    margin: 0 0 0 $divider-margin-x;\n  }\n}\n\n// Blockquote\n.blockquote {\n  padding: $blockquote-padding-y $blockquote-padding-x;\n  border-left: 0.25rem solid #eceeef;\n  &.text-right {\n    border-left: none;\n    border-right: 0.25rem solid #eceeef;\n  }\n  .bq-title {\n    margin-bottom: 0;\n    font-size: $font-size-large;\n    font-weight: 400;\n  }\n  p {\n    padding: $blockquote-p-padding-y 0;\n    font-size: $blockquote-p-font-size;\n  }\n}\n\n@each $name, $color in $basic {\n  .bq-#{$name} {\n    border-left: 3px solid $color !important;\n    .bq-title {\n      color: $color !important;\n    }\n  }\n}\n\n@each $name, $color in $basic-mdb-colors {\n  @include text-emphasis-variant('.text-#{$name}', $color);\n}\n\n.font-small {\n  font-size: $font-small;\n}\n","// Masks\n// General properties\n.view {\n  